import React from 'react';
import Input from './Input/Input';
import styles from './Form.module.scss';
import Button from '../Button/Button';
import Title from '../Title/Title';
import { useState } from 'react';
import Radio from './Radio/Radio';
import AppContext from '../../context';

const types = {
  twitter: 'twitter',
  article: 'article',
  note: 'note',
};

const description = {
  twitter: 'favorite twitter account',
  article: 'article',
  note: 'note',
};

const Form = () => {
  const [state, setState] = useState({
    type: types.twitter,
    title: '',
    link: '',
    image: '',
    description: '',
  });

  const handleRedioButtonChange = (type) => {
    setState({ type: type });
  };

  const handleInputChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  return (
    <AppContext.Consumer>
      {(context) => (
        <div className={styles.wrapper}>
          <Title>Add new {description[state.type]}</Title>
          <form
            autoComplete='off'
            className={styles.form}
            onSubmit={(e) => {
              context.addItem(e, state);
            }}
          >
            <div className={styles.radioWrapper}>
              <Radio
                isChecked={state.type === types.twitter}
                id={types.twitter}
                onChangeFn={() => handleRedioButtonChange(types.twitter)}
                label={types.twitter}
              />

              <Radio
                isChecked={state.type === types.article}
                id={types.article}
                onChangeFn={() => handleRedioButtonChange(types.article)}
                label={types.article}
              />

              <Radio
                isChecked={state.type === types.note}
                id={types.note}
                onChangeFn={() => handleRedioButtonChange(types.note)}
                label={types.note}
              />
            </div>
            <Input
              onChange={handleInputChange}
              value={state.title}
              name='title'
              label={state.type === types.twitter ? 'Name' : 'Title'}
              maxLength={30}
            />
            {state.type !== types.note ? (
              <Input
                onChange={handleInputChange}
                value={state.link}
                name='link'
                label='Link'
              />
            ) : null}
            {state.type === types.twitter ? (
              <Input
                onChange={handleInputChange}
                value={state.image}
                name='image'
                label='Image'
              />
            ) : null}
            <Input
              onChange={handleInputChange}
              value={state.description}
              tag='textarea'
              name='description'
              label='Description'
              maxLength={100}
            />
            <Button>add new item</Button>
          </form>
        </div>
      )}
    </AppContext.Consumer>
  );
};

export default Form;
