import { useState } from 'react';
import './index.css';
import AppContext from '../../context';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import ArticleView from '../ArticleView/ArticleView';
import NotesView from '../NotesView/NotesView';
import TwittersView from '../TwittersView/TwittersView';
import Header from '../../components/Header/Header';
import Modal from '../../components/Modal/Modal';
import React from 'react';

const Root = () => {
  const [stateItem, setStateItem] = useState([
    {
      type: 'twitter',
      title: 'hej',
      link: 'aszdasd',
      image:
        'https://scontent-waw1-1.xx.fbcdn.net/v/t1.0-9/74795485_2545688245518786_3139701456509075456_n.jpg?_nc_cat=111&ccb=1-3&_nc_sid=09cbfe&_nc_ohc=3haVM5NWChoAX8xfA58&_nc_ht=scontent-waw1-1.xx&oh=955464a5dffedba6bacada9e115fc108&oe=607F9DC7',
      description: 'asdasdasdasdasd',
    },
  ]);

  const xd = [...stateItem];
  const xd2 = xd.filter((item) => {
    return item.type === 'twitter';
  });

  const xd3 = xd.filter((item) => {
    return item.type === 'article';
  });

  const xd4 = xd.filter((item) => {
    return item.type === 'note';
  });

  const [isModalOpen, setIsModalOpen] = useState(true);

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const addItem = (e, newItem) => {
    e.preventDefault();

    const item = { ...newItem };
    setStateItem([...stateItem, item]);

    closeModal();
  };

  const contextElement = {
    stateItem,
    addItem: addItem,
  };

  return (
    <BrowserRouter>
      <AppContext.Provider value={contextElement}>
        <Header openModalFn={openModal} />
        <Switch>
          <Route
            exact
            path='/'
            component={() => <TwittersView items={xd2} />}
          />
          <Route
            path='/articles'
            component={() => <ArticleView items={xd3} />}
          />
          <Route path='/notes' component={() => <NotesView items={xd4} />} />
        </Switch>
        {isModalOpen && <Modal closeModalFn={closeModal} />}
      </AppContext.Provider>
    </BrowserRouter>
  );
};

export default Root;

